/* Content */
.dining-header {
    margin-top: 2rem !important;
    color: #ac051f !important;
    text-align: center;
    font-family: MiloSerifWeb, TimesNewRoman, "Times New Roman", Times, Baskerville, Georgia, serif;
    font-style: normal;
    font-weight: 700;
    text-transform: uppercase;
}
.dining-description {
    margin-top: 1rem;
    margin-bottom: 3rem;
}

/* Dining Btn */
.wrap-dining-btn {
    padding: 0px;
}
.wrap-dining-btn > a {
    display: block;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}
.wrap-dining-btn > a > img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    /* height: 75px; */
    border-radius: 7px;
}

/*  */
.wrap-hour, .wrap-phone {
    font-size: 23px;
    text-align: center;
}
.wrap-hour {
    margin-top: 3rem;
}
.wrap-click-all-hours {
    margin-top: 1.5rem;
    text-align: center;
}
.wrap-click-all-hours > a {
    color: #ac051f;
    font-size: 21px;
    font-style: italic;
    font-weight: 700;
    text-transform: uppercase;
}

/* SlideShow */
.wrap-slideshow {
    margin-top: 3rem;
}

.mobile-dining-menu-btn {
    display: none;
}
@media (max-width:767px){
    .mobile-dining-menu-btn {
        display: block;
    }
}
