.wrap-left-col-menu {
  /* margin-top: 2rem; */
  padding-bottom: 1.5rem !important;
}
.wrap-left-col-menu ul {
  border-top: 1px solid #b71234;
}
.wrap-dining-list > h2 {
    /* margin-bottom: 30px; */
    /* width: calc(100% - 63px); */
}
.wrap-dining-list > h2 > a, .wrap-dining-list > h2 > .dining-list-header {
    position: relative;
    display: block;
    padding-bottom: 20px;
    overflow: hidden;
    color: #333;
    font-size: 0.8em;
}
.wrap-dining-list > h2 > a > img, .wrap-dining-list > h2 > .dining-list-header > img {
    position: absolute;
    right: 0px;
    bottom: 0px;
}
.wrap-dining-list a.dining-list-link {
    display: block;
    margin-top: 0.2rem;
    color: #BB1F42;
}
.dining-list-header-text {
    display: block;
    margin-bottom: 5px;
    width: calc(100% - 0px);
}
span.dining-list-header-border {
    position: relative;
    /* display: block;
    width: calc(100% - 43px);
    border-bottom: 3px solid #BB1F42; */
}
span.dining-list-header-border > img {
    position: absolute;
    bottom: -18px;
}
p.dining-list-description {
    width: calc(100% - 0px);
}
/* Content */
.dining-header {
    margin-top: 2rem !important;
    color: #ac051f !important;
    text-align: center;
    font-family: MiloSerifWeb, TimesNewRoman, "Times New Roman", Times, Baskerville, Georgia, serif;
    font-style: normal;
    font-weight: 700;
    text-transform: uppercase;
}
.dining-description {
    margin-top: 1rem;
    margin-bottom: 3rem;
}

/* Dining Btn */
.wrap-dining-btn {
    padding: 0px;
}
.wrap-dining-btn > a {
    display: block;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}
.wrap-dining-btn > a > img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    /* height: 75px; */
    border-radius: 7px;
}

/*  */
.wrap-hour, .wrap-phone {
    font-size: 23px;
    text-align: center;
}
.wrap-hour {
    margin-top: 3rem;
}
.wrap-click-all-hours {
    margin-top: 1.5rem;
    text-align: center;
}
.wrap-click-all-hours > a {
    color: #ac051f;
    font-size: 21px;
    font-style: italic;
    font-weight: 700;
    text-transform: uppercase;
}

/* SlideShow */
.wrap-slideshow {
    margin-top: 3rem;
}

.mobile-dining-menu-btn {
    display: none;
}
@media (max-width:767px){
    .mobile-dining-menu-btn {
        display: block;
    }
}

