.wrap-dining-list > h2 {
    /* margin-bottom: 30px; */
    /* width: calc(100% - 63px); */
}
.wrap-dining-list > h2 > a, .wrap-dining-list > h2 > .dining-list-header {
    position: relative;
    display: block;
    padding-bottom: 20px;
    overflow: hidden;
    color: #333;
    font-size: 0.8em;
}
.wrap-dining-list > h2 > a > img, .wrap-dining-list > h2 > .dining-list-header > img {
    position: absolute;
    right: 0px;
    bottom: 0px;
}
.wrap-dining-list a.dining-list-link {
    display: block;
    margin-top: 0.2rem;
    color: #BB1F42;
}
.dining-list-header-text {
    display: block;
    margin-bottom: 5px;
    width: calc(100% - 0px);
}
span.dining-list-header-border {
    position: relative;
    /* display: block;
    width: calc(100% - 43px);
    border-bottom: 3px solid #BB1F42; */
}
span.dining-list-header-border > img {
    position: absolute;
    bottom: -18px;
}
p.dining-list-description {
    width: calc(100% - 0px);
}